<template>
  <div class="sales">
    <Navbar />
    <h1 class="catalog-title text-center m-4">
      Хиты продаж и скидки
    </h1>
    <form class="search">
      <input
        v-model="search"
        class="input-search"
        type="text"
        placeholder="Искать здесь..."
      >
      <button
        class="button-search"
        type="submit"
      >
        <img
          class="img-search"
          src="@/img/search.svg"
          alt
        >
      </button>
    </form>
    <div class="container p-0 mt-3">
      <div class="d-flex flex-row justify-content-center">
        <div class="col-xl col-lg-2 col-md-3 d-none d-md-block mr-3">
          <div class="row">
            <div class="col-xl-12 col-lg-12 col-md-12 d-none d-md-block p-0 mr-3 mb-3 mt-2">
              <div class="text-center banner1 p-0 m-0">
                <router-link to="/sales">
                  <img
                    draggable="false"
                    src="@/img/banner/153x515.png"
                    class="img-fluid"
                    style="height: 517px; width: 185px;"
                    alt
                  >
                </router-link>
              </div>
            </div>
            <div class="col-xl-12 col-lg-12 col-md-12 d-none d-md-block p-0 mr-3">
              <div class="text-center banner1 p-0 m-0">
                <a href="#">
                  <img
                    draggable="false"
                    src="@/img/banner/153x515.png"
                    class="img-fluid"
                    style="height: 275px; width: 185px;"
                    alt
                  >
                </a>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xl-8 col-lg-7 col-md-9 col-sm-12 col-12">
          <div class="row">
            <div
              v-for="(inf, i) in todosByTitle"
              :key="i"
              class="col-xl-4 col-lg-6 col-md-6 col-sm-6 col-6 d-flex justify-content-center mt-3"
            >
              <div class="card">
                <div class="sale" />
                <div class="m-1 p-1 d-flex align-items-center card-img-size">
                  <img
                    style="cursor: pointer"
                    draggable="false"
                    :src="`http://bohmann.shop${inf.images[0]}`"
                    class="img-fluid"
                    alt="..."
                    @click="goToproduct(inf.id)"
                  >
                </div>
                <div class="card-body p-1 d-flex align-items-center justify-content-center">
                  <h5
                    class="card-title text-center"
                    style="cursor: pointer"
                    @click="goToproduct(inf.id)"
                  >
                    {{ inf.name }}
                  </h5>
                </div>
                <div class="m-1">
                  <p class="card-text text-center">
                    {{ inf.price }} ₴
                    <b-img
                      v-if="log == 'true'"
                      right
                      class="favor"
                      title="В список желаний"
                      :src="require('../img/favorite.svg')"
                      @click="addToFavorite(inf)"
                    />
                  </p>
                  <button
                    :class="{ 'btn-block': !log }"
                    type="button"
                    class="btn btn-primary btn-block"
                    @click="addToCart(inf)"
                  >
                    Купить
                  </button>
                </div>
              </div>
            </div>
            <div
              class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12 d-flex justify-content-center mt-3"
            />
          </div>
          <div class="pagination">
            <a @click="firstPage">«</a>
            <a @click="Page1">1</a>
            <a @click="Page2">2</a>
            <a @click="Page3">3</a>
            <a @click="Page4">4</a>
            <a @click="lastPage">»</a>
          </div>
        </div>
        <div class="col-xl- col-lg-2 d-none d-lg-block ml-3 mt-2">
          <AccountWindow />
        </div>
      </div>
    </div>

    <Footer />
  </div>
</template>

<script>
import Navbar from "./Navbar.vue";
import Footer from "./Footer.vue";
import AccountWindow from "./AccountWindow.vue";
import axios from "axios";
export default {
    name: "Sale",
    components: {
        Navbar,
        Footer,
        AccountWindow
    },
    props: ["info", "user"],
    data() {
        return {
            search: "",
            log: this.$store.state.login,
            cc: []
        };
    },
    computed: {
        todosByTitle() {
            return this.cc.filter(item => item.name.indexOf(this.search) !== -1);
        }
    },
    mounted() {
        axios
            .get(`http://bohmann.shop/api/products/?limit=15`)
            .then(response => (this.cc = response.data.results));
    },
    methods: {
        addToFavorite(item) {
            this.$store.commit("addToFavorite", item);
            let foun = this.$store.state.favorite.find(
                product => product.id === item.id
            );
            foun.inFavorite = true;
        },
        goToproduct(Id) {
            this.$router.push({ name: "product", params: { Pid: Id } });
        },
        addToCart(item) {
            this.$store.commit("addToCart", item);
        },
        firstPage() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15`)
                .then(response => (this.cc = response.data.results));
        },
        Page1() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15`)
                .then(response => (this.cc = response.data.results));
        },
        Page2() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=15`)
                .then(response => (this.cc = response.data.results));
        },
        Page3() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=30`)
                .then(response => (this.cc = response.data.results));
        },
        Page4() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=45`)
                .then(response => (this.cc = response.data.results));
        },
        lastPage() {
            axios
                .get(`http://bohmann.shop/api/products/?limit=15&offset=45`)
                .then(response => (this.cc = response.data.results));
        }
    }
};
</script>
